import React, { useState } from "react";
import { S3Client, PutObjectCommand } from "@aws-sdk/client-s3";
import { fromStatic } from "@aws-sdk/credential-providers";

const UploadMenu = ({ closeMenu, setLoadedImages }) => {
  const [selectedFile, setSelectedFile] = useState(null);
  const [uploading, setUploading] = useState(false);

  // Replace with your S3 bucket configuration
  const s3Config = {
    region: "ap-south-1",
    bucketName: "framefinder-photography-abey",
    accessKeyId: process.env.NEXT_PUBLIC_AWS_ACCESS_KEY_ID, // Use environment variables
    secretAccessKey: process.env.NEXT_PUBLIC_AWS_SECRET_ACCESS_KEY,
  };

  const handleFileChange = (event) => {
    const file = event.target.files[0];
    if (file) {
      const allowedTypes = ["image/jpeg", "image/png"];
      if (allowedTypes.includes(file.type)) {
        setSelectedFile(file);
      } else {
        alert("Only JPG and PNG files are allowed.");
        event.target.value = ""; // Clear the file input
      }
    }
  };

  const handleUpload = async () => {
    if (!selectedFile) {
      alert("Please choose a file before uploading.");
      return;
    }

    setUploading(true);

    // Initialize S3 Client with fromStatic for credentials
    const s3Client = new S3Client({
      region: s3Config.region,
      credentials: fromStatic({
        accessKeyId: s3Config.accessKeyId,
        secretAccessKey: s3Config.secretAccessKey,
      }),
    });

    // Prepare the upload command
    const uploadParams = {
      Bucket: s3Config.bucketName,
      Key: `photos/${selectedFile.name}`, // File name in the S3 bucket
      Body: selectedFile, // File content
      ContentType: selectedFile.type, // MIME type
    };

    try {
      // Upload the file
      const command = new PutObjectCommand(uploadParams);
      await s3Client.send(command);

      // Re-fetch the images after a successful upload
      const response = await fetch("http://localhost:3001/api/images");
      if (!response.ok) {
        throw new Error(`Failed to fetch images: ${response.statusText}`);
      }
      const data = await response.json();
      setLoadedImages(data.data); // Update the state with the fetched images

      alert("File uploaded successfully!");
      closeMenu(); // Automatically close the upload menu
    } catch (error) {
      console.error("Error uploading file or fetching images:", error);
      alert("Failed to upload file. Please try again.");
    } finally {
      setUploading(false);
    }
  };

  return (
    <div>
      <div style={{ marginBottom: "20px" }}>
        <label
          htmlFor="file-input"
          style={{
            cursor: "pointer",
            color: "#007BFF",
            textDecoration: "underline",
          }}
        >
          Choose File
        </label>
        <input
          id="file-input"
          type="file"
          accept=".jpg,.png"
          style={{ display: "none" }}
          onChange={handleFileChange}
        />
      </div>
      <div>{selectedFile && <p>Selected File: {selectedFile.name}</p>}</div>
      <button
        onClick={handleUpload}
        style={{ marginRight: "10px" }}
        disabled={uploading}
      >
        {uploading ? "Uploading..." : "Upload"}
      </button>
      <button onClick={closeMenu}>Close</button>
    </div>
  );
};

export default UploadMenu;
